import React, { useState } from "react";

const navLinksList = ["categories", "templates"]

const HomeSideBar = () => {

    const [selectedNavLink, setSelectedNavLink] = useState(navLinksList[0])

    return (
        <div className="flex flex-no-wrap">
            {/* Sidebar starts */}
            <div className="w-64 absolute sm:relative shadow flex-col justify-between flex">
                <div className="p-8">
                    {/* 
                    <div className="h-16 w-full flex items-center">
                            LOGO HERE
                    </div>
                     */}
                    <ul className="mt-12">
                        <li className="flex w-full justify-between text-gray-700 hover:text-gray-900 cursor-pointer items-center mb-6">
                            <div className="flex items-center">
                                <button
                                    className="w-full focus:bg-rightbackgroundcolor h-10"
                                    onClick={() => setSelectedNavLink(navLinksList[0])}
                                >
                                    <div className="flex items-center  hover:bg-rightbackgroundcolor  w-full hover:rounded-lg border-0 rounded-sm  pl-5 h-10 mb-1">
                                        <p className="text-lightGray font-medium pl-3">Categories</p>
                                    </div>
                                </button>
                            </div>
                        </li>
                        <li className="flex w-full justify-between text-gray-700 hover:text-gray-900 cursor-pointer items-center mb-6">
                            <div className="flex items-center">
                                <button
                                    className="w-full focus:bg-rightbackgroundcolor h-10"
                                    onClick={() => setSelectedNavLink(navLinksList[1])}
                                >
                                    <div className="flex items-center  hover:bg-rightbackgroundcolor  w-full hover:rounded-lg border-0 rounded-sm  pl-5 h-10 mb-1">
                                        <p className="text-lightGray font-medium pl-3">Templates</p>
                                    </div>
                                </button>
                            </div>
                        </li>
                    </ul>
                </div>

            </div>

            {/* Sidebar ends */}
            {/* Remove class [ h-64 ] when adding a card block */}
            <div className="container mx-auto py-10 h-64 md:w-4/5 w-11/12 px-6">
                {/* Remove class [ border-dashed border-2 border-gray-300 ] to remove dotted border */}
                <div className="w-full h-full rounded border-dashed border-2 border-gray-300">
                    <div className={selectedNavLink === navLinksList[0] ? "" : "hidden"}>
                        <div className="space-y-4 p-5">
                            <span className="block">1</span>
                            <span className="block">2</span>
                            <span className="block">3</span>
                        </div>
                        <div className="space-y-4 p-5">
                            <form className="w-full max-w-sm">
                                <div className="flex items-center border-b border-teal-500 py-2">
                                    <input className="appearance-none bg-transparent border-none w-full text-gray-700 mr-3 py-1 px-2 leading-tight focus:outline-none" type="text" placeholder="Jane Doe" aria-label="Full name" />
                                    <button className="flex-shrink-0 bg-teal-500 hover:bg-teal-700 border-teal-500 hover:border-teal-700 text-sm border-4 text-white py-1 px-2 rounded" type="button">
                                        Sign Up
                                    </button>
                                    <button className="flex-shrink-0 border-transparent border-4 text-teal-500 hover:text-teal-800 text-sm py-1 px-2 rounded" type="button">
                                        Cancel
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                    <div className={selectedNavLink === navLinksList[1] ? "" : "hidden"}>
                        Templates
                    </div>
                </div>
            </div>
        </div>
    );
}

export default HomeSideBar;
